.portfolioItem-overlay {
    position: fixed;
    background-color: black;
    z-index: 1;
    width: 100%;
    height: 100%;
    opacity: 50%;
}

.portfolioItem-container {
    width: 70%;
    background: #331627;
    top: 20%;
    bottom: 20%;
    left: 15%;
    position: fixed;
    z-index: 2;
    text-align: center;
    border-radius: 15px;
}

.portfolioItem-expanded {
    width: 100%;
    margin: auto;
    height: 100%;
    object-fit: contain;
}

.pattern {
    background-color: #5a2846;
    height: 100px;
}

.background {
    background-color: #F2F2F2;
}

.content-container {
    width: 85%;
    margin: auto;
    padding-top: 30px;
}




/* rating */

:root {
    --star-size: 25px;
    --star-color: rgb(180, 175, 175);
    --star-background: #2ca55e;
}

.stars {
    --percent: calc(var(--rating) / 5 * 100%);
    display: inline-block;
    font-size: var(--star-size);
    /* font-family: Times; */
    line-height: 1;
}

.stars:before {
    content: '★★★★★';
    letter-spacing: 3px;
    background: linear-gradient(90deg, var(--star-background) var(--percent), var(--star-color) var(--percent));
    background-clip: content-box;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}


/* rating */


/* user header */


/* content-tab */

.content-tab {
    background-color: #5a2846;
    color: white;
    height: 60px;
}
.content-tab a {
    text-decoration: none;
    color: #fafafa;
    text-align: center;
}
.content-tab button, .content-tab a {
    border: none;
    background-color: initial;
    padding-top: 10px;
}

.content-tab .pi {
    font-size: 20px;
    padding: 4px 20px;
}

.content-tab .tab-titles {
    font-size: 12px;
    font-weight: 600;
    color: #fff;
}

.tab-portfolio {
    display: none;
}

.portfolio-items {
    box-shadow: 1px 0px 2px #88888861;
    border: none;
    border-radius: 3px;
    height: 70px;
}

.portfolio-items:hover {
    transform: scale(1.4);
    transition: all 0.3s;
}


/* content-tab */


/* content-body */

.sectionHeader .p-card-title {
    padding-left: 1rem;
    padding-top: 1rem;
    color: #2ca55e;
    font-size: 1rem !important;
    margin-bottom: 1rem!important;
    padding-bottom: 9px;
    border-bottom: 1px solid #5a284657;
    display: flex;
    justify-content: space-between;
    padding-right: 0.7rem;
}

.sectionHeader .p-card-title .pi {
    padding-right: 0.5rem;
    font-weight: 600;
}

.pi-pencil {
    color: #2ca55e;
    font-weight: 600;
    cursor: pointer;
}

.pi-trash {
    color: rgb(255, 64, 64);
    font-weight: 600;
    font-size: 20px;
    cursor: pointer;
}

.personalInfo-edit {
    color: #5a2846;
}

.pi-plus {
    cursor: pointer;
}

.p-card-body {
    padding-top: 0rem !important;
    font-size: 0.8rem;
    padding-bottom: 1.5rem !important;
}

.content-rightPanel {
    padding: 0.5rem 0.5rem 0 0 !important;
}

.mainTitle {
    color: black !important;
    display: flex;
    justify-content: space-between;
    padding-right: 2rem;
    align-items: flex-end;
}

.experienceCompany {
    color: #5a2846;
}

.skilltag {
    background-color: #5a2846;
    color: white;
    padding: 0.3rem 0.5rem;
    border-radius: 15px;
    font-weight: 600;
    margin: 0 10px 10px 0;
}

.skilltag .pi {
    font-size: 10px;
    padding-right: 5px;
    font-weight: 600;
}

.listStyle {
    list-style: none;
}

.listStyle li::before {
    content: "•";
    color: #2ca55e;
    padding-right: 5px;
}

.companyURL {
    color: #5a2846;
    font-weight: 600;
}


/* content-body */


/* edit page */

.pi-angle-double-left {
    vertical-align: baseline;
}

.backButton {
    cursor: pointer;
}

.inputLabel {
    font-size: 14px;
    font-weight: 600;
    color: #5a2846;
}

.p-inputtext:enabled:focus,
.p-dropdown:not(.p-disabled).p-focus {
    box-shadow: 0 0 0 0.2rem #5a28465c;
}

.inputField {
    width: 100%;
}

.skillInput {
    width: fit-content;
    margin-top: 10px;
    margin-bottom: 50px;
}

.skillInput .p-dropdown {
    border-color: #5a2846;
    border-radius: 5px 1px 1px 5px;
    min-width: 150px;
}

.skillInput .p-inputtext {
    padding-right: 2.5rem;
}

.skillInput .pi-times,
.skillInput .pi-chevron-down {
    font-size: 12px;
}

.skillInput .pi-times {
    padding-top: 3px;
}

.skillInput .pi-plus {
    padding: 10px;
    border: 3px solid #5a2846;
    border-radius: 0px 5px 5px 0px;
    vertical-align: bottom;
    background-color: #5a2846;
    color: white;
}

.tag-container {
    border: none;
}

.tag-container .p-tag .p-tag-value {
    margin-left: 8px;
}

.p-formgroup-inline {
    display: inline-flex;
}

.editPortfolio-container {
    position: relative;
    border-radius: 6px;
}

.editPortfolio-container .p-mx-2 {
    background-color: #5a284617;
    height: 220px;
    max-height: 220px;
    display: flex;
    border-radius: 20px;
}

.editPortfolio-container .p-mx-2>label {
    height: fit-content;
    margin: auto;
}

.editPortfolio-container .p-mx-2 img,
.portfolio-items img {
    height: 100%;
    width: 100%;
    object-fit: contain;
}

.portfolioItem-add {
    font-size: 80px;
    cursor: pointer;
}

.portfolioItem-icons {
    position: absolute;
    right: 20px;
    top: 10px;
}

.portfolioItem-icons>button, .portfolioItem-icons>a {
    background-color: transparent;
    padding: 6px;
    border: 0;
}

.width-100 {
    width: 100%;
}

.portfolioItem-icons>button:hover,
.portfolioItem-icons>button:focus,
.portfolioItem-icons>a:hover,
.portfolioItem-icons>a:focus {
    background-color: transparent !important;
    box-shadow: none;
    border: 0;
}

.portfolioItem-icons .pi-ellipsis-h {
    color: #5a2846;
    font-weight: 900;
    font-size: 20px;
}

.p-overlaypanel-content {
    background-color: #5a2846;
    padding: 0!important;
}

.p-overlaypanel:before {
    border: 0;
}

.p-overlaypanel:after {
    border-bottom-color: #5a2846;
}

.p-overlaypanel-content .pi,
.p-overlaypanel-content div {
    color: white;
}

.p-overlaypanel-content div:hover {
    background-color: #0000004f;
    cursor: pointer;
}

.editMode-footer {
    display: flex;
    justify-content: space-between;
    margin: 15px 5px 0 5px;
}

#saveButton {
    background-color: #2ca55e;
    border: none;
}


/* edit page */

@media (max-width: 768px) {
    .content-container {
        width: 95%;
    }
    .content-rightPanel {
        padding-left: 0.5rem;
    }
    .content-rightPanel {
        padding: 0 0.5rem !important;
    }
}

@media (max-width: 1160px) {
    .content-container {
        padding-top: 14px;
    }
    .userProfile-header {
        margin-bottom: 14px;
    }
    .username {
        font-size: 20px;
    }
     :root {
        --star-size: 15px;
    }
    .portfolio-panel {
        display: none;
    }
    .tab-portfolio {
        display: inline-block;
    }
    .profile-picture {
        width: 90px;
        height: 90px;
    }
    .profilePic-label .pi-camera {
        font-size: 15px;
        padding: 3px
    }
    .content-smallscreen {
        width: 100%;
    }
    .content-leftPanel {
        padding-bottom: 0;
    }
}